:root {
  --clr-primary: hsl(197, 67%, 29%);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin-top: 0;
}

body {
  margin: 0;
  min-height: 100vh;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
}